.data-carousel-item img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.data-carousel-item {
  width: 100%;
  height: 100%;
  position: relative;
}

*, :before, :after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, button, input {
  letter-spacing: 1.4px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
}

.background {
  min-height: 100vh;
  display: flex;
}

.container {
  flex: 0 700px;
  margin: auto;
  padding: 10px;
}

.screen {
  background: #3e3e3e;
  border-radius: 15px;
  position: relative;
}

.screen:after {
  content: "";
  z-index: -1;
  border-radius: 15px;
  display: block;
  position: absolute;
  inset: 0 20px;
  box-shadow: 0 20px 40px #0006;
}

.screen-header {
  background: #4d4d4f;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.screen-header-left {
  margin-right: auto;
}

.screen-header-button {
  background: #f5f0f0;
  border-radius: 8px;
  width: 8px;
  height: 8px;
  margin-right: 3px;
  display: inline-block;
}

.screen-header-button.close {
  background: #d70d72;
}

.screen-header-button.maximize {
  background: #e8e925;
}

.screen-header-button.minimize {
  background: #74c54f;
}

.screen-header-right {
  display: flex;
}

.screen-header-ellipsis {
  background: #fcfbfb;
  border-radius: 8px;
  width: 3px;
  height: 3px;
  margin-left: 2px;
}

.screen-body {
  display: flex;
}

.screen-body-item {
  flex: 1;
  padding: 50px;
}

.screen-body-item.left {
  flex-direction: column;
  display: flex;
}

.app-title {
  color: #f6f3f4;
  flex-direction: column;
  font-size: 26px;
  display: flex;
  position: relative;
}

.app-title:after {
  content: "";
  background: #ada5a8;
  width: 25px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.app-contact {
  color: #f9f7f7;
  margin-top: auto;
  font-size: 8px;
}

.app-form-group {
  margin-bottom: 15px;
}

.app-form-group.message {
  margin-top: 40px;
}

.app-form-group.buttons {
  text-align: right;
  margin-bottom: 0;
}

.app-form-control {
  color: #ddd;
  text-transform: uppercase;
  background: none;
  border: none;
  border-bottom: 1px solid #666;
  outline: none;
  width: 100%;
  padding: 10px 0;
  font-size: 14px;
  transition: border-color .2s;
}

.app-form-control::placeholder {
  color: #666;
}

.app-form-control:focus {
  border-bottom-color: #ddd;
}

.app-form-button {
  color: #f1ebed;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  font-size: 14px;
}

.app-form-button:hover {
  color: #b9134f;
}

.credits {
  color: #fffdfd;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-family: Roboto Condensed, sans-serif;
  font-size: 16px;
  font-weight: normal;
  display: flex;
}

.credits-link {
  color: #fff;
  align-items: center;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}

.dribbble {
  width: 20px;
  height: 20px;
  margin: 0 5px;
}

@media screen and (width <= 520px) {
  .screen-body {
    flex-direction: column;
  }

  .screen-body-item.left {
    margin-bottom: 30px;
  }

  .app-title {
    flex-direction: row;
  }

  .app-title span {
    margin-right: 12px;
  }

  .app-title:after {
    display: none;
  }
}

@media screen and (width <= 600px) {
  .screen-body {
    padding: 40px;
  }

  .screen-body-item {
    padding: 0;
  }
}

